// Get coordinates for a location (mock implementation)
export function getCoordinatesForLocation(location: string): { lat: number; lng: number } {
  const normalizedLocation = location.toLowerCase();
  
  // Check if location matches any known city
  for (const [city, coords] of Object.entries(mockCoordinates)) {
    if (normalizedLocation.includes(city.toLowerCase())) {
      return coords;
    }
  }
  
  // Return default Chennai coordinates if not found
  return mockCoordinates['Chennai'];
}

// Validate Indian phone number
export function isValidIndianPhone(phone: string): boolean {
  const phoneRegex = /^[6-9]\d{9}$/;
  return phoneRegex.test(phone);
}

// Generate JWT token (mock implementation)
export function generateToken(userId: string, userType: 'user' | 'admin'): string {
  const payload = {
    userId,
    userType,
    exp: Date.now() + (24 * 60 * 60 * 1000) // 24 hours
  };
  
  // In production, use proper JWT library
  return btoa(JSON.stringify(payload));
}

// Verify JWT token (mock implementation)
export function verifyToken(token: string): { userId: string; userType: 'user' | 'admin' } | null {
  try {
    const payload = JSON.parse(atob(token));
    
    if (payload.exp < Date.now()) {
      return null; // Token expired
    }
    
    return {
      userId: payload.userId,
      userType: payload.userType
    };
  } catch {
    return null;
  }
}
